
.footer{
	position: fixed;
	left: 0;
    bottom: 0;
    width: 100%;
    height: 55px;
    background:#CCCCCC;
}
.footer ul{
	width: 100%;
	height: 100%;
	display: -webkit-box;
}
.footer ul li{
	-webkit-box-flex: 1;
	display: -webkit-box;
    -webkit-box-pack: center;
    -webkit-box-align: center;
}




