
@import "/static/css/swiper.min.css";
@import "/static/css/bootstrap.min.css";
.good {
  height: 1000px;
}
.swiperImg {
  width: 100%;
  padding-top: 45px;
  overflow: hidden;
}
.swiperImg img {
  display: block;
  width: 100%;
  height: auto;
}
.sp-content {
  padding: 0 10px;
}
.sp-dl {
  width: 33.33%;
  float: left;
}
.sp-dl dt {
  text-align: center;
}
.sp-dl dt img {
  width: 74px;
  height: 74px;
  display: block;
  margin: 0 auto;
}
.sp-dl dd {
  width: 80%;
  margin: 0 auto;
}
.sp-dl dd h5 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
  font-size: 14px;
}
.sp-dl dd p {
  font-size: 14px;
}


