
.header {
  width: 100%;
  height: 45px;
  background: #f00;
  position: fixed;
  top: 0;
  left: 0;
  line-height: 45px;
  text-align: center;
  z-index: 9999999;
}
.left,
.right {
  width: 45px;
  height: 45px;
  background: #000;
  float: left;
}
.right {
  float: right;
  background: #686868;
}


